footer {
    display: flex;
    background-color: black;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 60px 0 30px 0;
    gap: 30px;
    margin-top: auto;
    text-align: center;
  
    span {
      color: white;
      font-size: var(--fs-m);
      font-weight: 500;
    }
  }
  
  @media (max-width: 767.98px) {
    footer {
      padding: 66px 120px 58px 122px;
      gap: 10px;
      span {
        font-size: 12px;
      }
    }
  }