.card {
    max-width: 340px;
    height: 340px;
    background: $primary;
    border-radius: 10px;
    overflow: hidden;
    margin: 0;
  
    &:hover {
      transition: 0.5s;
      transform: scale(1.1);
    }
  
    img {
      width: 100%;
      height: 100%;
      object-fit: cover;
      filter: brightness(0.7);
    }
  
    h2 {
      font-size: var(--fs-sm);
      position: relative;
      bottom: 80px;
      left: 20px;
      color: white;
      font-weight: 500;
      max-width: 80%;
      text-shadow: 1px 1px 1px #000000;
    }
  }
  
  @media (max-width: 767.98px) {
    .card {
      height: 255px;
      max-width: 100%;
  
      &:hover {
        transition: 0;
        transform: scale(1);
      }
    }
  }