.logement {
    display: flex;
    flex-direction: column;
    gap: 20px;
    max-width: 100%;
  
    p {
      color: $primary;
      margin: 0;
      font-size: var(--fs-sm);
      font-weight: 500;
    }
    .carrousel img {
      max-width: $max-screen-lg;
      width: 100%;
      max-height: 415px;
      object-fit: cover;
      filter: brightness(80%);
      flex-shrink: 0;
    }
  
    h2 {
      color: $primary;
      font-size: var(--fs-l);
      font-weight: 500;
      margin: 0;
    }
  
    .logement-collapses {
      display: flex;
      gap: 76px;
      margin-bottom: 50px;
  
      .collapse-header {
        border-radius: 10px;
      }
  
      .collapse-content ul {
        padding: 20px;
        margin: 0;
        background: $greyLight;
        border-radius: 5px;
  
        li {
          color: $primary;
          font-size: var(--fs-sm);
        }
      }
    }
  
    .logement-title {
      display: flex;
      justify-content: space-between;
      align-items: center;
  
      .host {
        display: flex;
        gap: 10px;
        align-self: baseline;
  
        p {
          max-width: 100px;
          text-align: right;
          margin: 0;
          align-self: center;
        }
  
        img {
          width: 64px;
          height: 64px;
          border-radius: 64px;
          background: $greyDark;
        }
      }
    }
  
    .tags-and-rating {
      display: flex;
      justify-content: space-between;
      align-items: center;
  
      .tags-container {
        display: flex;
        gap: 10px;
        margin-bottom: 20px;
      }
    }
  }
  
  @media (max-width: 767.98px) {
    .logement {
      h2 {
        margin-bottom: 15px;
      }
      p {
        font-size: 14px;
      }
      .host p {
        font-size: 12px;
      }
      .star {
        width: 18px;
      }
      .tags-and-rating {
        display: inline;
      }
    }
  
    .collapse .collapse-content p {
      font-size: 14px;
    }
  
    .logement .logement-collapses {
      flex-direction: column;
      gap: 20px;
      margin-bottom: 27px;
      .collapse-content ul li {
        font-size: 14px;
      }
    }
  
    .logement .logement-title .host {
      position: relative;
      top: 140px;
      position: relative;
      margin-bottom: 50px;
  
      img {
        width: 32px;
        height: 32px;
      }
    }
  }